.Intro {
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}

/* left side styling */
.i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.i-name> :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}

.i-name> :nth-child(2) {
    color: #027ea7;
    font-weight: bold;
    font-size: 3rem;
}

.i-name> :nth-child(3) {
    font-weight: 100;
    font-size: 18px;
    color: var(--gray);
    margin-top: 30px;
}

.i-button {
    width: 8rem;
    height: 3rem;

}

.i-icons {
    margin-top: 3rem;
    display: flex;
    gap: 0rem;
}

.i-icons>* {
    transform: scale(0.5);

}

.i-icons>*:hover {
    cursor: pointer;
}




/* right side styling */
.i-right {
    flex: 1;
    position: relative;
}

.i-right>* {
    position: absolute;
    z-index: 1;
}

.i-right> :nth-child(1) {
    transform: scale(0.68);
    left: -50%;
    top: -45%;
}


/* blur */
.blur {
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(212px);
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}


@media screen and (max-width: 820px) {

    .Intro {
        display: flex;
        height: 55vh;
        margin-top: 6rem;
    }

    .i-right {
        transform: scale(0.8);
        left: -3rem;
    }


    .i-right> :nth-child(1) {
        transform: scale(0.56);
        left: -28rem;
        top: -40%;

    }


    .i-right .blur {
        display: none;
    }

    .floating-div:nth-of-type(1) {
        /* display: none; */
        top: -7rem !important;
        ;
    }

}

@media screen and (max-width: 480px) {
    .Intro {
        flex-direction: column;
        gap: 7rem;
        height: 64rem;
    }

    .i-right {
        transform: scale(0.8);
        left: -3rem;
    }


    .i-right> :nth-child(1) {
        transform: scale(0.56);
        left: -28rem;
        top: -90%;

    }


    .i-right .blur {
        display: none;
    }

    .floating-div:nth-of-type(1) {
        /* display: none; */
        top: -7rem !important;
        ;
    }

    .floating-div:nth-of-type(2) {
        /* display: none; */
        top: 14rem !important;
        ;
    }

}