:root {
    --yellow: #F5C32C;
    --orange: #FCA61F;
    --black: #242D49;
    --gray: #788097;
    --blueCard: #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}



/* style for whole  */
.App {
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);
    font-family: 'DynaPuff', cursive;

}


.button {
    background: linear-gradient(180deg, #f07369 26.71%, #f07369 99.36%);
    border-radius: 34px;
    border: none;
    color: white;
    font-size: 16px;
    padding: 11px 26px 11px 26px;
}

.button:hover {
    background: #fedab6;
    cursor: pointer;
    color: #f07369;
}

@media screen and (max-width: 480px) {
    .App {
        padding: 0.5rem 1rem;

    }
}